
import { defineComponent } from '@vue/composition-api';
import DictionarySelectorNew from '@/views/components/DictionarySelectorNew.vue';

type SamplePrice = {
  samplePrice: number,
  samplePriceUnit: string,
  samplePriceNumber: string
};

export default defineComponent({
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    disabled: {
      type: Boolean,
    },
    value: {
      type: Object,
      default: () => {
        return {
          samplePrice: 0,
          samplePriceUnit: '',
          samplePriceNumber: '',
        };
      },
    },
  },
  render() {
    return (
      <div class="flex">
        <el-input
          disabled={this.disabled}
          vModel={this.value.samplePrice} type="string"
        />
        <DictionarySelectorNew
          disabled={this.disabled}
          vModel={this.value.samplePriceUnit}
          type="priceUnit"
          valueType="number"
          style="width: 180px; margin-left: 12px;"
        />
        <DictionarySelectorNew
          disabled={this.disabled}
          vModel={this.value.samplePriceNumber}
          type="sizeNumber"
          valueType="number"
          style="width: 180px; margin-left: 12px;"
        />
      </div>
    );
  },
  setup() {

  },
});
