
import i18n from '@/i18n';
import { defineComponent, reactive, ref } from '@vue/composition-api';

const orderDetail = defineComponent({
  props: {
    value: {
      type: Array as () => string[],
      default: () => [] as string[],
    },
    disableAdd: {
      type: Boolean,
      default: true,
    },
    disableDelete: {
      type: Boolean,
      default: true,
    },
    disabled: {
      type: Boolean,
    },
  },
  render() {
    const { value } = this;
    return (
      <div>
        <el-form style={{
          width: '100%',
        }} size="small">
          {
            value.map((item, idx) => <div class="flex" style={{ width: '100%' }}>
              <el-form-item label={`${i18n.t('order_management.material_dynamic_form.revise')}${idx + 1}`} style={{ width: '100%' }}>
                <el-input type="textarea" vModel={value[idx]} rows={4} resize="none" maxlength="500"
                  show-word-limit disabled={this.disabled}></el-input>
              </el-form-item>
              <div class="space-between" style={{
                width: '56px', padding: '0 0 0 10px', marginTop: '32px', marginBottom: '16px',
              }}>
                <div vShow={this.isAbleToDelete(idx) && !this.disabled} onclick={() => value.splice(idx, 1)}>
                  <i class="iconfont iconshanchu1 icon-link-class" />
                </div>
                <div vShow={value.length - 1 === idx && !this.disabled} onclick={() => value.splice(idx + 1, 0, '')}>
                  <i class="iconfont icontianjia icon-link-class" />
                </div>
              </div>
            </div>)
          }
        </el-form>
        <div class="divider"></div>
      </div>
    );
  },
  setup(props) {
    if (!props.value.length) {
      props.value.push('');
    }
    const form = reactive({
      color: [],
    });
    const originLength = props.value.length;

    function isAbleToDelete(index: number) {
      return index > originLength - 1;
    }
    return {
      form,
      isAbleToDelete,
    };
  },

});

export default orderDetail;
