




























import { defineComponent, onMounted, ref } from '@vue/composition-api';

const materialColorPicker = [
  {
    name: 'Red', cnName: '红', heximal: '#f00', active: false,
  },
  {
    name: 'Orange', cnName: '橙', heximal: '#FF6300', active: false,
  },
  {
    name: 'Yellow', cnName: '黄', heximal: '#FFE50F', active: false,
  },
  {
    name: 'Green', cnName: '绿', heximal: '#3EED4F', active: false,
  },
  {
    name: 'Blue', cnName: '蓝', heximal: '#2FB9F3', active: false,
  },
  {
    name: 'Purple', cnName: '紫', heximal: '#DE54E8', active: false,
  },
  {
    name: 'Black', cnName: '黑', heximal: '#000', active: false,
  },
  {
    name: 'Gray', cnName: '灰', heximal: '#808080', active: false,
  },
  {
    name: 'White', cnName: '白', heximal: '#fff', active: false,
  },
];

export default defineComponent({
  name: 'MaterialColorPicker',
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    mode: {
      type: String,
      default: '',
    },
    value: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const colors = ref(materialColorPicker);

    // 选择不同颜色时，把当前选择的颜色 emit 到父组件
    function changeActiveStatus(colorName: string, emitable = true) {
      if (props.disabled) return;
      // colors.forEach(color => {
      //   if (color.name === colorName) {
      //     color.active = true;
      //     if (emitable) emit('change', colorName, color, colors);
      //   } else {
      //     color.active = false;
      //   }
      // });
      const colorItem = colors.value.find(color => color.name === colorName);
      if (colorItem) {
        colorItem.active = !colorItem.active;
        if (emitable) {
          const colorValue = colors.value.filter(color => color.active).map(color => color.name).join(',');
          emit('change', colorValue, colorItem, colors.value);
        }
      }
    }
    // 初始化颜色状态
    function initActiveStatus(colorFamily: string) {
      colorFamily.split(',').forEach(color => {
        changeActiveStatus(color, false);
      });
    }

    onMounted(() => initActiveStatus(props.value));

    return {
      colors,
      changeActiveStatus,
    };
  },
});
