
import { defineComponent } from '@vue/composition-api';
import DictionarySelectorNew from '@/views/components/DictionarySelectorNew.vue';

export type ValueWithUnit = {
  value: number | string,
  unit: string
};

type SelectType = 'date' | 'thickness' | 'mqqUnit';

export default defineComponent({
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String as () => SelectType,
      required: true,
    },
    value: {
      type: Object as () => ValueWithUnit,
      default: () => {
        return {
          value: 1,
          unit: '',
        };
      },
    },
    width: {
      type: String,
      default: '560px',
    },
  },
  render() {
    return (
      <div class="flex" style={{
        width: this.width,
      }}>
        <el-input
          style={{ width: '80%' }}
          disabled={this.disabled}
          v-model={this.value.value}
          maxlength="18"
        />
        <DictionarySelectorNew
          style={{
            width: '20%',
            marginLeft: '12px',
          }}
          disabled={this.disabled}
          vModel={this.value.unit}
          type={this.type}
          valueType="number"
        />
      </div>
    );
  },
});
