import ColorType from '@/components/ColorType.vue';
import ModifyRemark from '@/components/ModifyRemark.vue';
import CustomInput from '@/components/CustomInput.vue';
import DateHourPicker from '@/components/DateHourPicker.vue';
import DictionarySelectorNew from '@/views/components/DictionarySelectorNew.vue';
import SamplePriceInput from '@/views/components/SamplePriceInput.vue';
import MaterialColorPicker from '@/views/components/ColorPicker.vue';
import InputWithUnit from '@/views/components/InputWithUnit.vue';
import DictionaryCountrySelector from '@/components/dictionary/DictionaryCountrySelector.vue';
import VueInputTag from '@/components/vue-input-tag.vue';
import JSONschema2Type from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import Jsonschema from '@4dst-saas/form-utils/dist/components/jsonschema2form/JSONschema';
import i18n from '@/i18n';

function formItemGenerator(this: InstanceType<typeof JSONschema2Type>, jss: Jsonschema, form: {
  [key: string]: any
}, key: string, attrs: Partial<Jsonschema>, h = this.$createElement) {
  h;
  // console.log(this);
  if (jss.custom) {
    const pAttrs = this.$attrs;
    switch (jss.customType) {
      case 'ColorType':
        return <ColorType vModel={this.form[key]} { ...{ attrs: pAttrs } } ref={jss.ref}/>;
      case 'ModifyRemark':
        return <ModifyRemark vModel={this.form[key]} { ...{ attrs: pAttrs } }/>;
      // case 'VueInputTag':
      //   return <InputTag vModel={this.form[key]} { ...{ attrs: pAttrs, readonly: pAttrs.disabled } }/>;
      case 'CustomInput':
        return <CustomInput vModel={this.form[key]} { ...{ attrs: pAttrs } } ref={jss.ref}/>;
      case 'DateHourPicker':
        return <DateHourPicker vModel={this.form[key]} { ...{ attrs: pAttrs } } width='560px'/>;
      case 'DictionarySelectorMateiral':
        return <DictionarySelectorNew
          vModel={this.form[key]}
          type="materialType"
          valueType="number"
          width="560px"
          { ...{ attrs: pAttrs }}
        />;
      case 'SamplePriceInput':
        return <SamplePriceInput width="560px"
        vModel={this.form[key] } { ...{ attrs: pAttrs } }/>;
      case 'InputWithMoq':
        return <InputWithUnit vModel={this.form[key]} type="mqqUnit" { ...{ attrs: pAttrs } }/>;
      case 'MaterialColorPicker':
        return <MaterialColorPicker { ...{ attrs: pAttrs } }
          vModel={this.form[key]} mode="tooltip" />;
      case 'InputWithDate':
        return <InputWithUnit vModel={this.form[key]} type="date" { ...{ attrs: pAttrs } }/>;
      case 'InputWithThickness':
        return <InputWithUnit vModel={this.form[key]} type="thicknessUnit" { ...{ attrs: pAttrs } } />;
      case 'DictionaryCountrySelector':
        return <DictionaryCountrySelector vModel={this.form[key]} { ...{ attrs: pAttrs } } />;
      case 'VueInputTag':
        return <VueInputTag innerTags={this.form[key]}
        placeholder={i18n.t('order_management.sample_dynamic_form.tag_placeholder')}
        { ...{ attrs: pAttrs } } readOnly={pAttrs.disabled} props={jss}/>;
        //   // eslint-disable-next-line no-case-declarations
        //   const { baseString, from, to } = jss as Jsonschema & {
        //     baseString?: string,
        //     from: number,
        //     to: number
        //   };
        //   // eslint-disable-next-line no-case-declarations
        //   const options:string[] | number[] = [];
        //   if (baseString) {
        //     for (let i = from; i <= to; i++) {
        //       options.push(`${baseString}${i}`);
        //     }
        //   } else {
        //     for (let i = from; i <= to; i++) {
        //       options.push(i);
        //     }
        //   }
        //   return <Selector2 options={options} vModel={this.form[key]}></Selector2>;
        // case 'cascader':
        //   // // eslint-disable-next-line no-case-declarations
        //   // let values = [] as string[];
        //   // // eslint-disable-next-line no-case-declarations
        //   // const { ref } = jss;
        //   // slot = <WrappedCascader ref={ref} val={values} onchange={(v: string[]) => {
        //   //   values = v;
        //   //   this.form[key] = v[v.length - 1];
        //   // }}></WrappedCascader>;

      //   // eslint-disable-next-line no-case-declarations
      //   const { ref } = jss;
      //   slot = <WrappedCascader {...{ attrs }} ref={ref} vModel={this.form[key]}></WrappedCascader>;
      //   return slot;
      // case 'numberUnitArray':
      //   return <NumberUnitArray list={this.form[key]}></NumberUnitArray>;
      // case 'dict':
      default: {
        return null;
        // const {
        //   url, params, headers, valueKey, labelKey,
        // } = jss;
        // const props = {};
        // Object.assign(props, {
        //   url, params, headers, valueKey, labelKey,
        // });
        // Object.keys(props).forEach(k => {
        //   if (props[k] === undefined || props[k] === null) delete props[k];
        // });
        // slot = <DictionarySelector props={props} vModel={this.form[key]}></DictionarySelector>;
        // return slot;
      }
    }
  }
  return undefined;
}

export default formItemGenerator;
