
import i18n from '@/i18n';
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
  model: {
    prop: 'value',
    event: 'change',
  },
  props: {
    value: {
      type: Number,
    },
    width: {
      type: String,
      default: '446px',
    },
  },
  render() {
    return (
      <el-date-picker style={{
        width: this.width,
      }}
        type='datetime'
        oninput={
          (date: string) => { this.$emit('change', new Date(date).getTime()); }
        }
        value={this.value}
        suffix-icon="el-icon-date"
        value-format="timestamp"
        format="yyyy-MM-dd HH:00"
        placeholder={i18n.t('order_management.sample_dynamic_form.date_placeholder')}
        picker-options={this.pickerOptions}
        popper-class='time-selector' />
    );
  },
  setup() {
    const current = new Date();
    const oneDay = 1000 * 60 * 60 * 24;
    const yesterday = +current - oneDay;

    function disabledDate(time: Date) {
      return +time < yesterday;
    }
    const pickerOptions = {
      // eslint-disable-next-line no-use-before-define
      disabledDate,
      selectableRange: '00:00:00 - 23:00:00',
      format: 'yyyy-MM-dd HH:00',
      start: 'yyyy-MM-dd 05:00',
      step: '01:00',
      end: '23:00',
    };
    return {
      pickerOptions,
    };
  },
});
