
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import { ColorType } from '@/components/dynamic-form/OrderSamples.vue';
import validateForm from '@/utils/validators/validate-form';
import strLength from '@/utils/validators/str-length';
import {
  defineComponent, reactive,
} from '@vue/composition-api';
import { ElForm } from 'element-ui/types/form';
import i18n from '@/i18n';

const rules = {
  colorNumber: validator2ElemRule(strLength, { max: 18 }),
  colorName: validator2ElemRule(strLength, { max: 18 }),
};

const ColorTypeInput = defineComponent({
  props: {
    value: {
      type: Array as () => ColorType[],
      default: () => [] as ColorType[],
    },
    disableAdd: {
      type: Boolean,
      default: true,
    },
    disableDelete: {
      type: Boolean,
      default: true,
    },
    disabled: {
      type: Boolean,
    },
  },
  render() {
    const { value } = this;
    return (
      <div>
        {
          value.map((item, idx) => <el-form size="small" rules={rules}
            props={{ model: item }}
            ref={`colorItem-${idx}`}>
            <div class="flex" style={{
              'justify-content': 'space-between',
              alignItems: 'flex-end',
            }}>
              <div class="flex space-between">
                <el-form-item props={{ prop: 'colorName', label: `${i18n.t('order_management.sample_dynamic_form.color_name')}${idx + 1}` }} style={{
                  width: '504px',
                }}>
                  <el-input vModel={value[idx].colorName} disabled={this.disabled}></el-input>
                </el-form-item>
                <el-form-item props={{ label: `${i18n.t('order_management.sample_dynamic_form.color_number')}${idx + 1}`, prop: 'colorNumber' }}>
                  <el-input vModel={value[idx].colorNumber} disabled={this.disabled}></el-input>
                </el-form-item>
              </div>
              <div class="flex space-between" style={{
                width: '56px', paddingLeft: '10px', marginBottom: '18px',
              }}>
                {
                  // 删除
                }
                <div vShow={this.isAbleToDelete(idx) && !this.disabled} onclick={() => value.splice(idx, 1)}>
                  <i class="iconfont iconshanchu1 icon-link-class" />
                </div>
                <div vShow={value.length - 1 === idx && !this.disabled}
                  onclick={() => value.splice(idx + 1, 0, new ColorType())}>
                  <i class="iconfont icontianjia icon-link-class" />
                </div>
              </div>
            </div>
          </el-form>)
        }
        {
          // <el-button type="primary" size="small" round onclick={
          //   () => this.validate()
          // }>校验</el-button>
        }
      </div>
    );
  },
  setup(props) {
    if (!props.value.length) {
      props.value.push(new ColorType());
    }
    const originLength = props.value.length;
    const form = reactive({
      color: [],
    });
    const plength = props.value.length;
    function isAbleToDelete(index: number) {
      return index > originLength - 1;
    }
    async function validate(this: InstanceType<typeof ColorTypeInput>) {
      if (props.value.length) {
        await Promise.all(props.value.map(async (_, idx) => {
          const res = await validateForm(this.$refs[`colorItem-${idx}`] as ElForm);
          return res;
        }));
      }
    }

    return {
      form,
      plength,
      originLength,
      isAbleToDelete,
      validate,
    };
  },
});

export default ColorTypeInput;
