
import i18n from '@/i18n';
import strLength from '@/utils/validators/str-length';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import validateForm from '@/utils/validators/validate-form';
import { defineComponent, ref } from '@vue/composition-api';
import { ElForm } from 'element-ui/types/form';

const rules = {
  inputLabel: validator2ElemRule(strLength, { max: 18, message: '最多输入18个字符串' }),
  inputValue: validator2ElemRule(strLength, { max: 18, message: '最多输入18个字符串' }),
};

const customInputs = defineComponent({
  props: {
    value: {
      type: Array as () => { inputLabel: string, inputValue: string }[],
      default: () => {
        return [
          { inputLabel: `${i18n.t('order_management.material_dynamic_form.customeize')}1`, inputValue: '' },
          { inputLabel: '自定义2', inputValue: '' },
          { inputLabel: '自定义3', inputValue: '' },
          { inputLabel: '自定义4', inputValue: '' },
          { inputLabel: '自定义5', inputValue: '' },
        ];
      },
    },
    disabled: {
      type: Boolean,
    },
  },
  render() {
    return (
      <div class="flex" style={{
        width: '100%',
        justifyContent: 'space-between',
        flexWrap: 'wrap',
      }} >
        {
          this.value.map((c, idx) => <el-form size="small" rules={rules} ref={`customInput-${idx}`}
            props={{ model: c }}
          >
            <div style={{
              width: '100px',
              marginVottom: '0px',
              height: '32px',
              lineHeight: '32px',
              display: 'flex',
              alignItems: 'center',
            }} vShow={!this.editList[idx]} onclick={
              () => {
                if (!this.disabled) {
                  this.editList.splice(idx, 1, !this.editList[idx]);
                  // this.editList[idx] = !this.editList[idx];
                }
              }
            }>
              <div class="text-one-line" style="max-width: 80px;">{this.value[idx].inputLabel}</div>
              <i class="iconfont icon-rename" style={{ color: '#999', marginLeft: '4px' }} />
            </div>
            <el-form-item style={{ width: '100px' }} prop="inputLabel"
              vShow={this.editList[idx]} class='custom-lable-input'>
              <el-input vModel={this.value[idx].inputLabel} onblur={
                () => {
                  if (!this.disabled) {
                    this.editList.splice(idx, 1, !this.editList[idx]);
                    // this.editList[idx] = !this.editList[idx];
                  }
                }}></el-input>
            </el-form-item>
            <el-form-item prop="inputValue" >
              <el-input vModel={this.value[idx].inputValue} disabled={this.disabled}></el-input>
            </el-form-item>
          </el-form>)
        }
        {
          // <el-button type="primary" size="small" round onclick={
          //   () => this.validate()
          // }>校验</el-button>
        }
      </div>
    );
  },
  setup(props) {
    // const edit = ref(false);
    const editList = ref([false, false, false, false, false]);

    async function validate(this: InstanceType<typeof customInputs>) {
      if (props.value.length) {
        // editList.value[0] = true;
        // editList.value.forEach(e => { e = true; });
        await Promise.all(props.value.map(async (_, idx) => {
          editList.value.splice(idx, 1, true);
          const res = await validateForm(this.$refs[`customInput-${idx}`] as ElForm);
          console.log(res);
          return res;
        }));
        // this.editList.forEach(e => { e = false; });
      }
    }
    return {
      // edit,
      editList,
      validate,
    };
  },
});
export default customInputs;
